/**
 * ErrorBoundary  (Error Boundary Component)
 *
 * 
 * (Catches errors in child components to prevent app crash)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { Button, Result } from 'antd'
import PropTypes from 'prop-types'

/**
 * ErrorBoundary 
 *
 * @example
 * <ErrorBoundary>
 *   <YourComponent />
 * </ErrorBoundary>
 */
class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hasError: false,
      error: null,
      errorInfo: null,
    }
  }

  /**
   * 
   * (Static method: Update state when error occurs)
   */
  static getDerivedStateFromError(error) {
    return {
      hasError: true,
      error,
    }
  }

  /**
   * 
   * (Called when component catches error)
   */
  componentDidCatch(error, errorInfo) {
    //  (Log error info)
    console.error('ErrorBoundary caught an error:', error, errorInfo)

    this.setState({
      error,
      errorInfo,
    })

    //  (Can send error to monitoring service)
    // Example: logErrorToService(error, errorInfo)
  }

  /**
   *  (Reset error state)
   */
  handleReset = () => {
    this.setState({
      hasError: false,
      error: null,
      errorInfo: null,
    })
  }

  /**
   *  (Reload page)
   */
  handleReload = () => {
    window.location.reload()
  }

  render() {
    const { hasError, error } = this.state
    const { children, fallback } = this.props

    //  (If error exists, show error UI)
    if (hasError) {
      //  (Custom error UI)
      if (fallback) {
        return typeof fallback === 'function'
          ? fallback(error, this.handleReset)
          : fallback
      }

      //  (Default error UI)
      return (
        <div className="error-boundary">
          <Result
            status="error"
            title="Oops! Something went wrong"
            subTitle=" / Sorry, something went wrong with the page"
            extra={[
              <Button type="primary" key="reset" onClick={this.handleReset}>
                Try Again / 
              </Button>,
              <Button key="reload" onClick={this.handleReload}>
                Reload Page / 
              </Button>,
            ]}
          >
            {process.env.NODE_ENV === 'development' && error && (
              <div className="error-boundary__details">
                <h4>Error Details (Development Only):</h4>
                <pre>{error.toString()}</pre>
              </div>
            )}
          </Result>
        </div>
      )
    }

    //  (Render children normally)
    return children
  }
}

ErrorBoundary.propTypes = {
  children: PropTypes.node.isRequired,
  fallback: PropTypes.oneOfType([
    PropTypes.node,
    PropTypes.func,
  ]),
}

export default ErrorBoundary

