/**
 * Loading  (Loading Component)
 *
 * 
 * (Provides various loading animations)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { Spin } from 'antd'
import { LoadingOutlined } from '@ant-design/icons'
import { useLanguage } from '@contexts/LanguageContext'
import PropTypes from 'prop-types'

/**
 * Loading 
 *
 * @param {Object} props - 
 * @param {boolean} props.spinning - 
 * @param {string} props.tip - 
 * @param {string} props.size -  (small/default/large)
 * @param {boolean} props.fullscreen - 
 * @param {ReactNode} props.children - 
 *
 * @example
 * // 
 * <Loading spinning={true} />
 *
 * // 
 * <Loading spinning={true} fullscreen={true} />
 *
 * // 
 * <Loading spinning={isLoading}>
 *   <YourContent />
 * </Loading>
 */
function Loading({
  spinning = true,
  tip,
  size = 'default',
  fullscreen = false,
  children,
}) {
  const { t } = useLanguage()

  //  (Default tip text)
  const defaultTip = tip || t('common.loading')

  //  (Custom loading icon)
  const loadingIcon = <LoadingOutlined style={{ fontSize: getSizeValue(size) }} spin />

  //  (Fullscreen loading)
  if (fullscreen) {
    return spinning ? (
      <div className="app-loading--fullscreen">
        <Spin
          indicator={loadingIcon}
          tip={defaultTip}
          size={size}
        />
      </div>
    ) : null
  }

  //  (Wrapper loading)
  if (children) {
    return (
      <Spin
        spinning={spinning}
        indicator={loadingIcon}
        tip={defaultTip}
        size={size}
        className="app-loading--wrapper"
      >
        {children}
      </Spin>
    )
  }

  //  (Simple loading)
  return (
    <div className="app-loading">
      <Spin
        indicator={loadingIcon}
        tip={defaultTip}
        size={size}
      />
    </div>
  )
}

/**
 *  (Get size value)
 * @param {string} size - 
 * @returns {number} 
 */
function getSizeValue(size) {
  const sizeMap = {
    small: 16,
    default: 24,
    large: 32,
  }
  return sizeMap[size] || 24
}

Loading.propTypes = {
  spinning: PropTypes.bool,
  tip: PropTypes.string,
  size: PropTypes.oneOf(['small', 'default', 'large']),
  fullscreen: PropTypes.bool,
  children: PropTypes.node,
}

export default Loading

