/**
 *  (Language Context)
 *
 * 
 * (Provides global language switching and translation)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React, { createContext, useContext, useState, useCallback } from 'react'
import zhLang from '../lang/zh'
import enLang from '../lang/en'
import { ConfigProvider } from 'antd'
import zhCN from 'antd/locale/zh_CN'
import enUS from 'antd/locale/en_US'

//  (Language pack mapping)
const languages = {
  zh: zhLang,
  en: enLang,
}

// Ant Design  (Ant Design locale mapping)
const antdLocales = {
  zh: zhCN,
  en: enUS,
}

//  Context (Create context)
const LanguageContext = createContext()

/**
 *  (Language Provider Component)
 *
 * @param {Object} props - 
 * @param {ReactNode} props.children - 
 */
export function LanguageProvider({ children }) {
  //  localStorage  (Read language from localStorage)
  const [language, setLanguage] = useState(() => {
    const saved = localStorage.getItem('language')
    return saved || 'zh'
  })

  /**
   *  (Toggle language)
   */
  const toggleLanguage = useCallback(() => {
    const newLang = language === 'zh' ? 'en' : 'zh'
    setLanguage(newLang)
    localStorage.setItem('language', newLang)

    //  HTML lang  (Update HTML lang attribute)
    document.documentElement.lang = newLang === 'zh' ? 'zh-CN' : 'en'
  }, [language])

  /**
   *  (Set language)
   *
   * @param {string} lang -  (zh/en)
   */
  const setLang = useCallback((lang) => {
    if (lang !== language && languages[lang]) {
      setLanguage(lang)
      localStorage.setItem('language', lang)
      document.documentElement.lang = lang === 'zh' ? 'zh-CN' : 'en'
    }
  }, [language])

  /**
   *  (Translation function)
   *
   * @param {string} key -  'common.confirm'
   * @param {Object} params -  {count: 10}
   * @returns {string} 
   */
  const t = useCallback((key, params) => {
    //  (Support nested keys with dot notation)
    const keys = key.split('.')
    let value = languages[language]

    for (const k of keys) {
      value = value?.[k]
      if (value === undefined) {
        console.warn(`Translation key not found: ${key}`)
        return key
      }
    }

    //  (Replace parameters if provided)
    if (params && typeof value === 'string') {
      return value.replace(/\{(\w+)\}/g, (match, paramKey) => {
        return params[paramKey] !== undefined ? params[paramKey] : match
      })
    }

    return value
  }, [language])

  /**
   *  (Get full language pack)
   *
   * @returns {Object} 
   */
  const getLangPack = useCallback(() => {
    return languages[language]
  }, [language])

  // Context  (Context value)
  const value = {
    language,        //  (current language)
    toggleLanguage,  //  (toggle function)
    setLanguage: setLang, //  (set language function)
    t,               //  (translation function)
    getLangPack,     //  (get language pack)
  }

  return (
    <LanguageContext.Provider value={value}>
      <ConfigProvider locale={antdLocales[language]}>
        {children}
      </ConfigProvider>
    </LanguageContext.Provider>
  )
}

/**
 *  Hook (Use language hook)
 *
 * @returns {Object} 
 *
 * @example
 * const { t, language, toggleLanguage } = useLanguage()
 * console.log(t('common.confirm')) // "" or "Confirm"
 */
export function useLanguage() {
  const context = useContext(LanguageContext)

  if (!context) {
    throw new Error('useLanguage must be used within LanguageProvider')
  }

  return context
}

export default LanguageContext

