/**
 * Header  (Header Navigation Component)
 *
 * 
 * (Provides application top navigation bar)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { Menu, Button } from 'antd'
import {
  HomeOutlined,
  MessageOutlined,
  FileOutlined,
  TeamOutlined,
  GlobalOutlined
} from '@ant-design/icons'
import { useLanguage } from '@contexts/LanguageContext'
import PropTypes from 'prop-types'

/**
 * Header 
 *
 * @param {Object} props - 
 * @param {string} props.activeKey - 
 * @param {Function} props.onMenuClick - 
 * @param {boolean} props.showLanguageToggle - 
 *
 * @example
 * <Header
 *   activeKey="qa"
 *   onMenuClick={(key) => console.log(key)}
 *   showLanguageToggle={true}
 * />
 */
function Header({
  activeKey = 'home',
  onMenuClick,
  showLanguageToggle = true
}) {
  const { t, language, toggleLanguage } = useLanguage()

  //  (Menu items configuration)
  const menuItems = [
    {
      key: 'home',
      icon: <HomeOutlined />,
      label: t('nav.home'),
    },
    {
      key: 'qa',
      icon: <MessageOutlined />,
      label: t('nav.qa'),
    },
    {
      key: 'documents',
      icon: <FileOutlined />,
      label: t('nav.documents'),
    },
    {
      key: 'roles',
      icon: <TeamOutlined />,
      label: t('nav.roles'),
    },
  ]

  return (
    <header className="app-header">
      <div className="app-header__container">
        {/* Logo  (Logo and title) */}
        <div className="app-header__logo">
          <span className="app-header__logo-icon"></span>
          <span className="app-header__logo-text">AI Reviewer</span>
        </div>

        {/*  (Navigation menu) */}
        <Menu
          mode="horizontal"
          selectedKeys={[activeKey]}
          items={menuItems}
          onClick={({ key }) => onMenuClick?.(key)}
          className="app-header__menu"
        />

        {/*  (Right actions) */}
        <div className="app-header__actions">
          {showLanguageToggle && (
            <Button
              icon={<GlobalOutlined />}
              onClick={toggleLanguage}
              className="app-header__language-btn"
            >
              {language === 'zh' ? 'EN' : ''}
            </Button>
          )}
        </div>
      </div>
    </header>
  )
}

Header.propTypes = {
  activeKey: PropTypes.string,
  onMenuClick: PropTypes.func,
  showLanguageToggle: PropTypes.bool,
}

export default Header

