/**
 * Footer  (Footer Component)
 *
 * 
 * (Provides application footer information)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { useLanguage } from '@contexts/LanguageContext'
import { GithubOutlined, HeartFilled } from '@ant-design/icons'

/**
 * Footer 
 *
 * @example
 * <Footer />
 */
function Footer() {
  const { language } = useLanguage()
  const currentYear = new Date().getFullYear()

  return (
    <footer className="app-footer">
      <div className="app-footer__container">
        {/*  (Copyright) */}
        <div className="app-footer__copyright">
          <span> {currentYear} AI Reviewer Team. </span>
          {language === 'zh' ? (
            <>
               <HeartFilled className="app-footer__heart" /> 
            </>
          ) : (
            <>
              Built with <HeartFilled className="app-footer__heart" />
            </>
          )}
        </div>

        {/*  (Links) */}
        <div className="app-footer__links">
          <a
            href="https://github.com"
            target="_blank"
            rel="noopener noreferrer"
            className="app-footer__link"
          >
            <GithubOutlined /> GitHub
          </a>
          <span className="app-footer__divider">|</span>
          <a
            href="/docs"
            className="app-footer__link"
          >
            {language === 'zh' ? '' : 'Documentation'}
          </a>
          <span className="app-footer__divider">|</span>
          <a
            href="/about"
            className="app-footer__link"
          >
            {language === 'zh' ? '' : 'About'}
          </a>
        </div>
      </div>
    </footer>
  )
}

export default Footer

