/**
 * Modal  (Modal Dialog Component)
 *
 *  Ant Design Modal
 * (Wraps Ant Design Modal for consistent dialogs)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { Modal as AntModal } from 'antd'
import { useLanguage } from '@contexts/LanguageContext'
import PropTypes from 'prop-types'

/**
 * Modal 
 *
 * @param {Object} props - 
 * @param {boolean} props.visible - 
 * @param {string} props.title - 
 * @param {ReactNode} props.children - 
 * @param {Function} props.onOk - 
 * @param {Function} props.onCancel - 
 * @param {string} props.okText - 
 * @param {string} props.cancelText - 
 * @param {boolean} props.confirmLoading - 
 * @param {string} props.width - 
 * @param {boolean} props.centered - 
 * @param {boolean} props.maskClosable - 
 *
 * @example
 * <Modal
 *   visible={visible}
 *   title=""
 *   onOk={handleOk}
 *   onCancel={handleCancel}
 * >
 *   <p></p>
 * </Modal>
 */
function Modal({
  visible,
  title,
  children,
  onOk,
  onCancel,
  okText,
  cancelText,
  confirmLoading = false,
  width = 520,
  centered = true,
  maskClosable = true,
  ...restProps
}) {
  const { t } = useLanguage()

  return (
    <AntModal
      open={visible}
      title={title}
      onOk={onOk}
      onCancel={onCancel}
      okText={okText || t('common.confirm')}
      cancelText={cancelText || t('common.cancel')}
      confirmLoading={confirmLoading}
      width={width}
      centered={centered}
      maskClosable={maskClosable}
      className="app-modal"
      {...restProps}
    >
      {children}
    </AntModal>
  )
}

Modal.propTypes = {
  visible: PropTypes.bool.isRequired,
  title: PropTypes.string,
  children: PropTypes.node,
  onOk: PropTypes.func,
  onCancel: PropTypes.func,
  okText: PropTypes.string,
  cancelText: PropTypes.string,
  confirmLoading: PropTypes.bool,
  width: PropTypes.oneOfType([PropTypes.number, PropTypes.string]),
  centered: PropTypes.bool,
  maskClosable: PropTypes.bool,
}

/**
 *  (Confirm dialog)
 */
Modal.confirm = (config) => {
  AntModal.confirm({
    centered: true,
    className: 'app-modal',
    ...config,
  })
}

/**
 *  (Info dialog)
 */
Modal.info = (config) => {
  AntModal.info({
    centered: true,
    className: 'app-modal',
    ...config,
  })
}

/**
 *  (Success dialog)
 */
Modal.success = (config) => {
  AntModal.success({
    centered: true,
    className: 'app-modal',
    ...config,
  })
}

/**
 *  (Error dialog)
 */
Modal.error = (config) => {
  AntModal.error({
    centered: true,
    className: 'app-modal',
    ...config,
  })
}

/**
 *  (Warning dialog)
 */
Modal.warning = (config) => {
  AntModal.warning({
    centered: true,
    className: 'app-modal',
    ...config,
  })
}

export default Modal

