/**
 *  (Main Application Component)
 *
 * 
 * (Manages overall application state, layout, and routing)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React, { useState } from 'react'
import { Button, Space, Tag } from 'antd'
import { CheckCircleOutlined } from '@ant-design/icons'
import { LanguageProvider, useLanguage } from './contexts/LanguageContext'
import { Layout, Header, Footer } from './components/layout'
import { ErrorBoundary, Loading } from './components/common'
import './assets/css/layout.css'
import './assets/css/header.css'
import './assets/css/footer.css'
import './assets/css/loading.css'
import './assets/css/error-boundary.css'

/**
 *  (App Content Component)
 *  (Uses language context)
 */
function AppContent() {
  const { t, language } = useLanguage()
  const [activeMenu, setActiveMenu] = useState('home')

  //  (Menu click handler)
  const handleMenuClick = (key) => {
    setActiveMenu(key)
    console.log('Navigate to:', key)
  }

  return (
    <ErrorBoundary>
      <Layout
        header={
          <Header
            activeKey={activeMenu}
            onMenuClick={handleMenuClick}
            showLanguageToggle={true}
          />
        }
        footer={<Footer />}
      >
        {/*  (Main content) */}
        <div className="welcome-message">
          <h2>
            <CheckCircleOutlined style={{ color: '#52c41a', marginRight: '8px' }} />
            {language === 'zh' ? ' Phase 7.2 ' : ' Phase 7.2 Components Development'}
          </h2>

          <Space direction="vertical" size="large" style={{ width: '100%' }}>
            {/*  (Completed features) */}
            <div className="feature-list">
              <h3>{language === 'zh' ? ' ' : ' Completed Components'}</h3>
              <ul>
                <li>
                  <Tag color="success">Layout</Tag>
                  {language === 'zh' ? '' : 'Layout container component'}
                </li>
                <li>
                  <Tag color="success">Header</Tag>
                  {language === 'zh' ? '' : 'Navigation bar with language toggle'}
                </li>
                <li>
                  <Tag color="success">Footer</Tag>
                  {language === 'zh' ? '' : 'Footer component'}
                </li>
                <li>
                  <Tag color="success">Loading</Tag>
                  {language === 'zh' ? '' : 'Loading animation component'}
                </li>
                <li>
                  <Tag color="success">ErrorBoundary</Tag>
                  {language === 'zh' ? '' : 'Error boundary component'}
                </li>
              </ul>
            </div>

            {/*  (Test area) */}
            <div className="test-area">
              <h3>{language === 'zh' ? ' ' : ' Component Testing'}</h3>
              <Space wrap>
                <Button type="primary">{t('common.confirm')}</Button>
                <Button>{t('common.cancel')}</Button>
                <Button type="dashed">{t('common.search')}</Button>
                <Button danger>{t('common.delete')}</Button>
              </Space>
            </div>

            {/* Loading  (Loading test) */}
            <div className="loading-demo">
              <h3>{language === 'zh' ? ' Loading ' : ' Loading Component Demo'}</h3>
              <Loading spinning={true} tip={t('common.loading')} />
            </div>
          </Space>
        </div>
      </Layout>
    </ErrorBoundary>
  )
}

/**
 *  (Main App Component)
 *  LanguageProvider (Wraps LanguageProvider)
 */
function App() {
  return (
    <LanguageProvider>
      <AppContent />
    </LanguageProvider>
  )
}

export default App
