/**
 * Layout  (Layout Component)
 *
 *  HeaderContentFooter
 * (Provides basic layout structure with Header, Content, Footer)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { Layout as AntLayout } from 'antd'
import PropTypes from 'prop-types'

const { Content } = AntLayout

/**
 * Layout 
 *
 * @param {Object} props - 
 * @param {ReactNode} props.children - 
 * @param {ReactNode} props.header - 
 * @param {ReactNode} props.footer - 
 * @param {string} props.className - 
 *
 * @example
 * <Layout
 *   header={<Header />}
 *   footer={<Footer />}
 * >
 *   <YourContent />
 * </Layout>
 */
function Layout({ children, header, footer, className = '' }) {
  return (
    <AntLayout className={`app-layout ${className}`}>
      {/*  (Header) */}
      {header && (
        <div className="app-layout__header">
          {header}
        </div>
      )}

      {/*  (Main content) */}
      <Content className="app-layout__content">
        {children}
      </Content>

      {/*  (Footer) */}
      {footer && (
        <div className="app-layout__footer">
          {footer}
        </div>
      )}
    </AntLayout>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
  header: PropTypes.node,
  footer: PropTypes.node,
  className: PropTypes.string,
}

export default Layout

