/**
 * Skeleton  (Skeleton Screen Component)
 *
 *  Ant Design Skeleton
 * (Wraps Ant Design Skeleton for loading placeholders)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { Skeleton as AntSkeleton } from 'antd'
import PropTypes from 'prop-types'

/**
 * Skeleton 
 *
 * @param {Object} props - 
 * @param {boolean} props.loading - 
 * @param {boolean} props.active - 
 * @param {boolean} props.avatar - 
 * @param {boolean} props.title - 
 * @param {number} props.paragraph - 
 * @param {ReactNode} props.children - 
 * @param {string} props.type -  (default/card/list)
 *
 * @example
 * <Skeleton loading={isLoading}>
 *   <YourContent />
 * </Skeleton>
 */
function Skeleton({
  loading = true,
  active = true,
  avatar = false,
  title = true,
  paragraph = 3,
  children,
  type = 'default',
  ...restProps
}) {
  //  (Return different skeleton based on type)
  const renderSkeleton = () => {
    switch (type) {
      case 'card':
        return (
          <div className="app-skeleton app-skeleton--card">
            <AntSkeleton
              active={active}
              avatar={{ size: 64, shape: 'square' }}
              title={title}
              paragraph={{ rows: paragraph }}
              {...restProps}
            />
          </div>
        )

      case 'list':
        return (
          <div className="app-skeleton app-skeleton--list">
            <AntSkeleton
              active={active}
              avatar={{ size: 40 }}
              title={title}
              paragraph={{ rows: 2 }}
              {...restProps}
            />
          </div>
        )

      default:
        return (
          <div className="app-skeleton">
            <AntSkeleton
              active={active}
              avatar={avatar}
              title={title}
              paragraph={{ rows: paragraph }}
              {...restProps}
            />
          </div>
        )
    }
  }

  if (!loading && children) {
    return children
  }

  return renderSkeleton()
}

Skeleton.propTypes = {
  loading: PropTypes.bool,
  active: PropTypes.bool,
  avatar: PropTypes.bool,
  title: PropTypes.bool,
  paragraph: PropTypes.number,
  children: PropTypes.node,
  type: PropTypes.oneOf(['default', 'card', 'list']),
}

/**
 * Button  (Button skeleton)
 */
Skeleton.Button = ({ active = true, size = 'default', ...props }) => (
  <div className="app-skeleton app-skeleton--button">
    <AntSkeleton.Button active={active} size={size} {...props} />
  </div>
)

/**
 * Image  (Image skeleton)
 */
Skeleton.Image = ({ active = true, ...props }) => (
  <div className="app-skeleton app-skeleton--image">
    <AntSkeleton.Image active={active} {...props} />
  </div>
)

/**
 * Input  (Input skeleton)
 */
Skeleton.Input = ({ active = true, size = 'default', ...props }) => (
  <div className="app-skeleton app-skeleton--input">
    <AntSkeleton.Input active={active} size={size} {...props} />
  </div>
)

export default Skeleton

