/**
 * Pagination  (Pagination Component)
 *
 *  Ant Design Pagination
 * (Wraps Ant Design Pagination for consistent pagination)
 *
 * @author AI Reviewer Team
 * @since 2025-12-12
 */

import React from 'react'
import { Pagination as AntPagination } from 'antd'
import PropTypes from 'prop-types'

/**
 * Pagination 
 *
 * @param {Object} props - 
 * @param {number} props.current - 
 * @param {number} props.total - 
 * @param {number} props.pageSize - 
 * @param {Function} props.onChange - 
 * @param {Function} props.onShowSizeChange - 
 * @param {Array} props.pageSizeOptions - 
 * @param {boolean} props.showSizeChanger - 
 * @param {boolean} props.showQuickJumper - 
 * @param {boolean} props.showTotal - 
 * @param {string} props.size -  (default/small)
 *
 * @example
 * <Pagination
 *   current={1}
 *   total={100}
 *   pageSize={10}
 *   onChange={handlePageChange}
 * />
 */
function Pagination({
  current = 1,
  total = 0,
  pageSize = 10,
  onChange,
  onShowSizeChange,
  pageSizeOptions = ['10', '20', '50', '100'],
  showSizeChanger = true,
  showQuickJumper = true,
  showTotal = true,
  size = 'default',
  ...restProps
}) {
  //  (Function to show total)
  const showTotalText = (total, range) => {
    return `${range[0]}-${range[1]} / ${total}`
  }

  return (
    <div className="app-pagination">
      <AntPagination
        current={current}
        total={total}
        pageSize={pageSize}
        onChange={onChange}
        onShowSizeChange={onShowSizeChange}
        pageSizeOptions={pageSizeOptions}
        showSizeChanger={showSizeChanger}
        showQuickJumper={showQuickJumper}
        showTotal={showTotal ? showTotalText : undefined}
        size={size}
        {...restProps}
      />
    </div>
  )
}

Pagination.propTypes = {
  current: PropTypes.number,
  total: PropTypes.number,
  pageSize: PropTypes.number,
  onChange: PropTypes.func,
  onShowSizeChange: PropTypes.func,
  pageSizeOptions: PropTypes.array,
  showSizeChanger: PropTypes.bool,
  showQuickJumper: PropTypes.bool,
  showTotal: PropTypes.bool,
  size: PropTypes.oneOf(['default', 'small']),
}

export default Pagination

